.Container {
    display: flex;
    /* background-color: red; */
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.CalculatorContainer {
    display: flex;
    position: relative;
    height: 95%;
    width: 95%;
    align-items: center;
    flex-direction: column;
}

.inputContainer {
    min-height: 25%;
    margin: 0% 2%;
    position: relative;
}

.inputContainer::before {
    position: absolute;
    content: url(../../../media/zCALCULATOR.png);
    background-size: 10px 20px;
    width: auto;
    left: 2%;
    top: 3vh;
    height: auto;

}

.inputContainer div,
.inputContainer .display {
    width: 100%;
    display: flex;
    overflow: hidden;
    justify-content: flex-end;
    align-items: flex-end;
    color: white;
    background-color: black;
    outline: none;
    border: none;
    caret-color: cyan;
    font-size: 12vh;
    text-align: right;
}

.display {
    color: cyan !important;
    position: absolute;
    -ms-overflow-style: none;
    /* width: fit-content !important; */
    scrollbar-width: none;
    font-size: 12vw;
    overflow-x: scroll !important;
}

.display::-webkit-scrollbar {
    display: none;
}

.inputContainer {
    padding: 1% 0;
}

.inputContainer .inputNum {
    font-size: 4vh;
    min-height: 5vh;
    /* max-width: 70vw; */
}

.features {
    font-size: 2vh;
    height: 13%;
}

.inputContainer,
.numsContainer {
    /* border: 1px solid red; */
    width: 49%;
}

.numsContainer {
    min-height: 75%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    position: relative;
}

.numsContainer::before {
    content: '';
    width: 100%;
    display: flex;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.341);

}

.Btn {
    flex: 25%;
    color: white;
    background: rgba(20, 20, 20, 0.568);
    outline: none;
    border: none;
    cursor: pointer;
    border: 0.1px solid rgba(255, 255, 255, 0.189);
    font-size: 30px;
    font-weight: 700;
}

.Btn:hover,
.Btn:focus {
    background-color: rgba(20, 20, 20, 0.294);
}

.small-btn-font {
    font-size: 28px;
    padding: 2% 5.9%;
}

.large-btn-font {
    padding: 2% 7.5% !important;
}

.orange {
    flex: 25% !important;
    color: orangered !important;
}

.green {
    color: green !important;
}

.bg-green {
    flex: 50% !important;
    background-color: green !important;
}

.bg-green:hover,
.bg-green:hover {
    background-color: rgba(0, 128, 0, 0.749) !important;
}

.delete-icon {
    position: absolute;
    right: 3px;
    bottom: 12px;
    font-size: 22px;
    color: green;
}

@media (max-width: 810px) {

    .inputContainer,
    .numsContainer {
        width: 100%;
    }

    .inputNum {
        margin: 0;
        text-align: end;
        align-items: flex-end;
    }
    .numsContainer{
        min-height: 71%;
    }

    .Btn {
        font-size: 20px;
    }

    .display{
        font-size: 11vh !important;
    }
}

@media (max-height:900px) {
    .display {
        padding-right: 5%;
    }

    .Btn {
        font-size: 15px;
    }

    .inputContainer::before {
        content: url(../../../media/zCALCULATOR1.png);
        top: 0;
    }

}